.quoraBox {
  display: flex;
  flex-direction: column;
  padding: 10px;
  border: 1px solid lightgray;
  /* background-color: white; */
  border-radius: 5px;
  cursor: pointer;
}

.quoraBox:hover {
  border: 1px solid rgb(175, 175, 175);
}

.quoraBox__info {
  display: flex;
  align-items: center;
}

.quoraBox__info > h5 {
  /* color: rgb(129, 129, 129); */
  font-weight: 300;
  margin-left: 10px;
}

.quoraBox__quora {
  display: flex;
  margin-top: 8px;
  justify-content: space-between;
}

.quoraBox__quora>button {
  margin-left: auto;
  cursor: pointer;
  padding: 4px 7px;
  background-color: #b92b27;
  outline: none;
  border: none;
  color: white;
  font-weight: 300;
  font-size: 14px;
  border-radius: 1rem;
}
.quoraBox__quora>button:hover {
  color: #b92b27;
  background-color: rgba(0, 0, 0, 0.04);
}

.quoraBox__quora > p {
  /* color: gray; */
  font-weight: bold;
  font-size: large;
}
